@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital@1&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-family:"Open Sans", sans-serif;background-color:#171d30}.container{margin:0 auto;width:80%;background-color:#171d30}.logo-container{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:2rem 0;color:white}.logo-container img{max-width:70%;max-height:70%}h1{color:white}.character-table{display:grid;overflow:auto;align-items:center;justify-content:center;grid-template-columns:repeat(3, auto);grid-gap:1rem;width:100%}@media screen and (max-width: 1200px){.character-table{grid-template-columns:repeat(2, auto)}}@media screen and (max-width: 760px){.character-table{grid-template-columns:auto}}.characterCard{border:0.1px solid #3c4b7c;background-color:#283253;color:white;position:relative;display:flex;margin:10px;width:270px;height:55px}.characterCard:hover{opacity:0.7;cursor:pointer}.character-name{padding:15.5px;background-color:#283253;text-transform:capitalize}.character-thumbnail{max-height:50px;position:absolute;right:1px;bottom:1px;object-fit:contain;-webkit-mask-image:linear-gradient(270deg, #000, transparent);mask-image:linear-gradient(270deg, #000, transparent)}.character-header{display:flex;color:white;margin:2rem 0;justify-content:space-between;border:0.1px solid #3c4b7c;width:100%}.character-header h2{font-size:2rem;padding:50px;text-transform:capitalize}.character-banner{height:150px;-webkit-mask-image:linear-gradient(270deg, #000, transparent);mask-image:linear-gradient(270deg, #000, transparent)}character-move-list{width:100%}table,th,td{border:0.1px solid #3c4b7c;color:white}th{font-weight:500;padding:10px;text-align:left;background-color:#283253}td{text-transform:capitalize;padding:10px}tr:nth-child(even){background-color:#283253}

